import React, { FC } from 'react'
import clsx from 'clsx'
import { LoadingIcon } from '../../icons/Loading'
import { ButtonProps } from './types'
import './Button.scss'

const Button: FC<ButtonProps> = ({
  className,
  id,
  disabled = false,
  icon = null,
  iconPosition = 'L',
  onClick,
  size = 'M',
  text,
  theme = 'Accent',
  isLoading = false,
  linkProps = {},
}) => {
  const isLink = Object.entries(linkProps).length

  const shouldRenderIconOnLeft =
    Boolean(icon) && iconPosition === 'L' && !isLoading
  const shouldRenderIconOnRight =
    Boolean(icon) && iconPosition === 'R' && !isLoading
  const shouldRenderIconOnly =
    Boolean(icon) && iconPosition === 'M' && !isLoading

  const buttonContent = shouldRenderIconOnly ? (
    icon
  ) : (
    <>
      <p
        className={clsx(
          'ButtonNext__Text',
          `ButtonNext__Text_Size-${size}`,
          Boolean(icon) && `ButtonNext__Text_With-Icon-${iconPosition}`,
        )}
      >
        {text}
      </p>
    </>
  )

  const buttonProps = {
    // Common props
    className: clsx(
      'ButtonNext',
      `ButtonNext_Size-${size}`,
      iconPosition === 'M' && `ButtonNext_Only-Icon`,
      `ButtonNext_Theme-${theme}`,
      isLoading && [
        'ButtonNext__With-Loader',
        `ButtonNext__With-Loader-${size}`,
      ],
      isLink && 'ButtonNext_Link',
      className,
    ),
    id,
    // Button only props
    disabled: isLink ? undefined : disabled || isLoading,
    onClick: isLink ? undefined : onClick,
    // Anchor only props
    ...linkProps,
  }

  return React.createElement(
    isLink ? 'a' : 'button',
    buttonProps,
    <>
      {shouldRenderIconOnLeft && icon}
      {isLoading ? (
        <LoadingIcon
          className={clsx(
            'ButtonNext__Loader',
            `ButtonNext__Loader_Theme-${theme}`,
          )}
        />
      ) : (
        buttonContent
      )}
      {shouldRenderIconOnRight && icon}
    </>,
  )
}

export default Button
